import { Plugin, IonicNativePlugin, Cordova } from '@ionic-native/core';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

/**
 * @name SmsInbox
 * @description
 * This plugin helps to operate on SMS on Android phones.
 *
 * @usage
 * ```typescript
 * import { SmsInbox } from '@ionic-native/sms-inbox';
 *
 *
 * constructor(private sms_inbox: SmsInbox) { }
 *
 * ...
 * this.sms_inbox.listSMS({ addresses:['Google', 'Facebook'] })
 *    .then(messages => console.log(`Found ${messages.length} from Google and Facebook`))
 *    .catch(error => console.log('Error getting messages', error))
 * ```
 */

export interface MessageFilters {
   _id?: number;
   read?: number;
   indexFrom?: number;
   maxCount?: number;
   address?: string;
   addresses?: string[];
   body?: string;
   contents?: string[];
}

@Plugin({
  pluginName: 'SmsInbox',
  plugin: 'cordova-plugin-sms',
  pluginRef: 'SMS',
  repo: 'https://github.com/ultractiv/cordova-plugin-sms',
  platforms: ['Android']
})

export class SmsInbox extends IonicNativePlugin {

  @Cordova()
  listSMS(filter: MessageFilters): Promise<any> { return; }

  @Cordova({
    observable: true,
    clearFunction: 'stopWatch'
  })
  startWatch(filter: MessageFilters): Observable<any> { return; }

  @Cordova()
  enableIntercept(on_off: boolean): Promise<any> { return; }

static decorators: DecoratorInvocation[] = [
{ type: Injectable },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
